.infoMain {
    display: flex;
    flex-flow: row nowrap;
    gap: 8px;
    align-items: end;
    height: fit-content;
}

.infoTitle {
    font-size: 20px;
    line-height: 18px;
    color: #111111;
    font-weight: 800;
}

.titleActive {
    padding: 5px;
    border-radius: 6px;
    cursor: pointer;
}

.titleActive:hover {
    background: rgba(119, 119, 119, 0.5);
}

.infoDescription {
    font-size: 13px;
    line-height: 13px;
    color: #444444;
    font-weight: 600;
}