.toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 28px;
    gap: 4px;
}

.item {
    height: 20px;
    width: 20px;
}

.mainContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    height: 26px;
    width: 52px;
    border-radius: 13px;
    background-color: #bababa;
    overflow: hidden;
    position: relative;
}

.item {
    width: 33.33%;
    height: 100%;
}

.switcher {
    position: absolute;
    height: 22px;
    width: 22px;
    border-radius: 11px;

    transition: transform ease-in;
    transition-duration: 0.2s;
}

.left {
    background-color: #9a0b00;
    transform: translate(-11px);
}

.center {
    background-color: #444444;
    transform: translate(0);
}

.right {
    background-color: #0b9a00;
    transform: translate(11px);
}
