.wrapper {
    width: 100%;
    background: #bababa;
    border-radius: 12px;

    padding: 8px;
    display: grid;
    gap: 8px;

    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 3fr 1fr 2fr 2fr 2fr;
}

.highlightedWrapper {
    border: 3px solid #E0115F;
}

.dateString {
    display: flex;
    flex-flow: row nowrap;
    justify-content: end;
}

.ceilString {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    gap: 8px;
}

.ceilButtons {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    gap: 4px;
}
