.wrapper {
    width: 100%;
}

.body {
    min-width: 600px;
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 4px;

    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    gap: 20px;
}

.menu_start {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: center;
    gap: 8px;
}

.menu_end {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end;
    align-items: center;
    gap: 8px;
}

.menuInput {
    display: flex;
    flex-flow: row nowrap;
    justify-content: start;
    align-items: center;
    gap: 4px;
}

.newline {
    flex-grow: 1;
    width: 100%;
}
