.wrapper {
    display: flex;
    flex-flow: column nowrap;
    align-items: end;
    gap: 4px;
}

.tierGroup {
    display: flex;
    flex-flow: row nowrap;
    gap: 4px;
    overflow: hidden;
    width: fit-content;
}

.tierItem {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px 6px;
    width: 72px;
    cursor: pointer;
    opacity: 0.75;
}
