.wrapper {
    width: 100%;
    background: #646464;
}

.body {
    min-width: 600px;
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 4px;

    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    gap: 20px;
}

.submenu {
    display: flex;
    flex-flow: row nowrap;
    justify-content: start;
    gap: 20px;
}

.link {
    font-size: 14px;
    color: #eeeeee;
    font-weight: 400;
    text-decoration: none;
}

.link_active {
    color: white;
    font-weight: 600;
}
