.wrapper {
    width: 100%;
    background: #bababa;
    padding: 8px;
    border-radius: 12px;

    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    gap: 12px;
}

.info {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    gap: 4px;
}
