.body {
    display: flex;
    flex-flow: row wrap;
    gap: 8px;
}

.itemElement {
    display: flex;
    flex-flow: row nowrap;
    gap: 4px;
    align-items: center;
}

.itemLabel {
    color: white;
    font-size: 14px;
}

.itemValue {
    color: white;
    font-size: 14px;
    padding: 4px;
    border-radius: 4px;
    background: rgba(50, 50, 50, 0.9);
}