.main_page {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    display: flex;
    flex-flow: column nowrap;
    background: #dedede;
}

.wrapper_page {
    flex-grow: 1;

    overflow-y: auto;
    overflow-x: hidden;
}

.body_page {
    min-width: 600px;
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 12px 4px;
}