.button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px 6px;
    cursor: pointer;
    opacity: 0.8;
    height: 28px;
    white-space: nowrap;
    border-radius: 6px;
}

.active_button {
}

.active_button:hover {
    opacity: 0.9 !important;
}

.active_button:active {
    opacity: 1 !important;
}
