.infoMain {
    display: flex;
    flex-flow: row nowrap;
    gap: 4px;
    justify-content: space-between;
    align-items: end;
    height: fit-content;
    width: 100%;
}

.infoGroup {
    display: flex;
    flex-flow: row nowrap;
    gap: 4px;
}

.infoPrefix {
    font-size: 14px;
    line-height: 14px;
    color: #444444;
    font-weight: 600;
    white-space: nowrap;
}

.infoTitle {
    font-size: 20px;
    line-height: 18px;
    color: #111111;
    font-weight: 800;
}

.infoPercent {
    font-size: 14px;
    line-height: 14px;
    color: #444444;
    font-weight: 600;
    height: 100%;
}
