.directionMain {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    height: fit-content;
    position: relative;
}

.item {
    z-index: 2;
}

.directionSecondary {
    z-index: 1;
    right: 10px;
    position: relative;
    font-size: 13px;
    line-height: 13px;
    color: #cccccc;
    font-weight: 600;
    background-color: #777777;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px 6px 4px 14px;
    height: 28px;
    border-radius: 6px;
    white-space: nowrap;
    width: fit-content;
    margin-right: -10px;

}