.modalBackground {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #333;
    opacity: 0.4;
    z-index: 2;
}

.modalContainer {
    min-height: 200px;
    max-height: 97vh;
    min-width: 600px;
    width: 94%;
    max-width: 1600px;
    overflow: hidden;
    position: fixed;
    top: 51%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #dedede;
    z-index: 3;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    box-shadow: 4px 4px 8px 0 rgb(34 60 80 / 20%);
}

.modalHeader {
    width: 100%;
    padding: 8px;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    gap: 4px;
    background: #cccccc;
    border-bottom: 1px solid #646464;

}

.flexRow {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    gap: 8px;
}

.modalBody {
    width: 100%;
    padding: 8px;
    flex-grow: 1;
    overflow-y: auto;
}

.tableBLock {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4px;
}
